
header{
  position: relative;
  /* FIXME: check if  auto prefix plugin works and remove extra declarations */
  background: rgba(22,21,49,1);
  background: linear-gradient(to right, rgba(22,21,49,1) 0%, rgba(68,74,122,1) 38%, rgba(68,74,122,1) 50%, rgba(68,74,122,1) 66%, rgba(22,21,49,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#161531', endColorstr='#161531', GradientType=1 );
}
.container {
  position: relative;
}
.header__gliter-left{
  position: absolute;
  top: -30px;
  left: 0;
}
.header__gliter-right{
  position: absolute;
  top: -30px;
  right: 0;
}
.header__title{
  margin-top: 40px;
  color: #c2c6ce;
  text-align: center;
  font-size: 35px;
}
.header__heart{
  position: absolute;
  bottom: -50px;
  left: -50px;
}
.header__north-star{
  position: absolute;
  bottom: 0;
  right: 0;
}
.header__red-cup{
  position: absolute;
  bottom: 0;
  left: 0;
}
span.line {
  display: inline-block;
}
.header__filter-text{
  font-size: 20px;
  margin-bottom: 20px;
  font-family: 'Rokkitt', serif;
  text-align: center;
  color: #c2c6ce;
}
.header__middle{
  max-width: 400px;
  position: relative;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}
.header__filters{
  margin-bottom: 20px;
  text-align: center;
}
.header__btn{
  transition: background-color 0.5s ease;
  border: 0px;
  padding: 10px;
  outline: none;
  margin: 5px;
  width: 200px;
  color: #012453;
  background-color: white;
}
.header__btn.selected{
  color: white;
  background-color: #7499c6;
}
.header__btn:hover{
  color: white;
  background-color: #7fa6d4;
}



